@import "./animations.css";

.player {
	position: relative;
	width: 50px;
	height: 50px;
    outline: none;
    cursor: pointer;
    margin-top: 20px;
    font-size: 16px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.player_caption { position: absolute; -webkit-transform: translate(0, -20px); -ms-transform: translate(0, -20px); transform: translate(0, -20px); white-space: nowrap; }
.player svg { width: 100%; height: 100%; }
	
.player[status="idle"] { color: rgba(0,0,0,.8) }
.player[status="loading"] .player_icon { opacity: 0; }
.player[status="loading"] .player_preloader { opacity: 1; -webkit-animation: rotate 1.5s linear infinite; animation: rotate 1.5s linear infinite; }
.player[status="loading"] .player_preloader circle { opacity: 1; -webkit-animation: dash 1.5s ease-in-out infinite; animation: dash 1.5s ease-in-out infinite; }
.player[status="playing"] { color: #DC3710 }

.player_preloader, .player_icon { 
    -webkit-transition: opacity .2s, color .2s; 
    -o-transition: opacity .2s, color .2s; 
    transition: opacity .2s, color .2s;
    position: absolute;
}

.player_preloader {
    opacity: 0;
    color: #DC3710;
    -webkit-transform-origin: center center;
        -ms-transform-origin: center center;
            transform-origin: center center;
}

.player_preloader circle {
    fill: none;
    stroke-width: 3; 
    stroke-miterlimit: 10;
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    stroke-linecap: round;
}


